.__apartma {
    display        : flex;
    justify-content: center;
    align-items    : center;
}

.hide {
    display: none;
}

.__apartma-text {
    padding: 0 4rem;
}

.__apartma-text h2 {
    font-size     : 35px;
    padding-bottom: 1rem;
}

.__apartma-text p {
    font-size  : 20px;
    line-height: 25px;
}

.slika {
    max-width: 700px;
}

.row {
    display        : flex;
    justify-content: center;
    align-items    : center;
    flex-direction : row;
}

.reverse {
    display        : flex;
    justify-content: center;
    align-items    : center;
    flex-direction : row-reverse;
}

@media screen and (max-width: 1300px) {

    .row,
    .reverse {
        flex-direction: column-reverse;
    }

    .slika {
        padding: 2rem 0 3rem 0;
    }
}

@media screen and (max-width: 700px) {

    .__apartma-text {
        padding: 0 3rem;
    }

    .__apartma-text h2 {
        font-size     : 30px;
        padding-bottom: 1rem;
    }

    .__apartma-text p {
        font-size  : 18px;
        line-height: 22px;
    }

    .slika {
        padding  : 1rem 0 2rem 0;
        max-width: 100%;
    }
}

@media screen and (max-width: 500px) {

    .__apartma-text {
        padding: 0 2rem;
    }

    .__apartma-text h2 {
        font-size     : 25px;
        padding-bottom: 1rem;
    }

    .__apartma-text p {
        font-size  : 16px;
        line-height: 22px;
    }

    .slika {
        padding  : 1rem 0 2rem 0;
        max-width: 100%;
    }
}