* {
    box-sizing     : border-box;
    padding        : 0;
    margin         : 0;
    scroll-behavior: smooth;
}

body {
    background-color: linear-gradient(90deg, rgba(243, 244, 246, 1) 0%, rgba(184, 246, 255, 1) 62%, rgba(0, 212, 245, 0.8631827731092436) 100%);
}

a {
    color          : unset;
    text-decoration: none;
}

.gradient__bg {

    /* ff 3.6+ */
    background: -moz-radial-gradient(circle at 7% 25%, rgba(243, 244, 246, 1) 0%, rgba(0, 212, 245, 0.86) 100%);

    /* safari 5.1+,chrome 10+ */
    background: -webkit-radial-gradient(circle at 7% 25%, rgba(243, 244, 246, 1) 0%, rgba(0, 212, 245, 0.86) 100%);

    /* opera 11.10+ */
    background: -o-radial-gradient(circle at 7% 25%, rgba(243, 244, 246, 1) 0%, rgba(0, 212, 245, 0.86) 100%);

    /* ie 10+ */
    background: -ms-radial-gradient(circle at 7% 25%, rgba(243, 244, 246, 1) 0%, rgba(0, 212, 245, 0.86) 100%);

    /* global 92%+ browsers support */
    background: radial-gradient(circle at 7% 25%, rgba(243, 244, 246, 1) 0%, rgba(0, 212, 245, 0.86) 100%);

}

.nav__color {
    background-color: var(--color-nav);
}

.section__padding {
    padding: 2rem 3rem;
}

.section__margine {
    margin: 4rem 6rem;
}

@media screen and (max-width: 700px) {
    .section__padding {
        padding: 2rem 2rem;
    }

    .section__margine {
        margin: 4rem;
    }
}

@media screen and (max-width: 550px) {
    .section__padding {
        padding: 2rem 2rem;
    }

    .section__margine {
        margin: 4rem 2rem;
    }
}